

























































































































































































































































































































































































.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.breadcrumbs{
  margin-left: 0 !important;
  color: white;
  width: 800px;
}
.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}
.set-row-height {
  height: 85px !important;
}
.overflow-td {
  overflow-y: auto;
  width: 150px;
  height: 65px;
  word-break: break-word;
}
.value {
  font-weight: bold;
  opacity: 0.6;
}
